@use '../../style/mixins';
@forward '../checkbox/checkbox.scss';
@forward '../list/radio-button/radio-button.scss';

*,
*:before,
*:after {
    box-sizing: border-box;
}

:host(limel-list-item:hover) {
    transition: background-color 0.2s ease;
}

:host(limel-list-item) {
    transition: background-color 0.6s ease;
    position: relative;
    isolation: isolate;
    box-sizing: border-box;

    display: flex;
    align-items: center;
    gap: 0.5rem;

    padding: 0 1rem;
    background-color: rgb(var(--contrast-100));
}

:host(limel-list-item[disabled]:not([disabled='false'])) {
    cursor: not-allowed;
    opacity: 0.4;
}

:host(limel-list-item:not([disabled]):not([disabled='true'])) {
}

:host(limel-list-item:not([disabled]):not([disabled='true']):hover),
:host(limel-list-item:not([disabled]):not([disabled='true']):focus-visible),
:host(limel-list-item:not([disabled]):not([disabled='true']):focus-within) {
    background-color: rgb(var(--contrast-400));
}

:host(limel-list-item[selected]:not([selected='false'])):before {
    cursor: pointer;
    content: '';
    position: absolute;
    inset: 0;

    background-color: var(--limel-theme-primary-color);
    opacity: 0.2;
    border-radius: inherit;
}

.text,
limel-icon,
img {
    pointer-events: none;
}

.text {
    flex-grow: 1;
    min-width: 0;

    display: flex;
    flex-direction: column;
    gap: 0.25rem;

    padding: 0.5rem 0;

    color: var(--limel-theme-text-primary-on-background-color);
}

label {
    font-size: var(--limel-theme-default-font-size);
}

.description {
    font-size: var(--limel-theme-default-small-font-size);
    opacity: 0.6;
}

limel-icon {
    flex-shrink: 0;
    // width: 1.5rem;

    &:has(+ img) {
        position: absolute;
        top: 0.125rem;
        left: 0.125rem;
        padding: 0.1875rem;
        width: 1.25rem;
        background-color: rgb(var(--contrast-200), 0.8);
    }
}

img {
    flex-shrink: 0;
    object-fit: cover;
    border-radius: 50%;
    width: 2rem;
    height: 2rem;
    box-shadow: 0 0 0 1px rgb(var(--contrast-800), 0.5);
}

:host(limel-list-item) {
    label {
        @include mixins.truncate-text();
    }

    .description {
        @include mixins.truncate-text-on-line(var(--maxLinesSecondaryText));
        line-height: 1rem;
    }
}

limel-menu {
    margin-right: -0.5rem;
    // Sometimes, `primaryComponent`s in list items need to get
    // `order: 2;` to be placed after the text. But since the default `order`
    // of all lit item children is `0`, we need to push the menu to the far right
    // by giving it a bigger order number. Also, by making this into a variable
    // we can handle other edge-cases in consuming components.
    order: var(--limel-list-item-menu-order, 3);
}

:host:has(limel-icon) {
    .boolean-input {
        order: 2;
    }
}

// Checkbox and radios
.boolean-input {
    // width: calc(
    //     var(--limel-boolean-input-box-size) +
    //         var(--limel-boolean-input-gap-size)
    // );
    width: var(--limel-boolean-input-box-size);
}

label.boolean-input-label {
    @include mixins.truncate-text();
    opacity: 0;
    width: var(--limel-boolean-input-box-size);
}
